function customQueryProducts(httpClient: HttpClient) {
  const router = createQueryOverloadRouter({
    builderQueryFunction: () => publicQueryProducts(httpClient)(),
    typedQueryFunction: (query: ProductQuery) =>
      publicTypedQueryProducts(httpClient)(query),
    hasOptionsParameter: false,
  });

  function overloadedQuery(): ProductsQueryBuilder;
  function overloadedQuery(
    query: ProductQuery
  ): ReturnType<typeof universalTypedQueryProducts>;
  function overloadedQuery(query?: ProductQuery): any {
    return router(...arguments);
  }

  return overloadedQuery;
}

